body {
  background: hsl(120, 4%, 24%);
  overflow: hidden;
}

@font-face {
  font-family: "Lato";
  src: url("assets/Lato-Regular.woff2") format("woff2"),
    url("assets/Lato-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

* {
  font-family: Lato, sans-serif;
  font-size: 9px;
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

input {
  border: none;
}



